import { Component } from "react";
import { connect } from "react-redux";
import {
    initFeatProj,
    initOtherProj,
    initDesignProj,
} from "../store/actions/projects";

import { MainContainer } from "../components/UI/utils";
import FeaturedProjects from "../components/Projects/FeatProj";
import OtherProjects from "../components/Projects/OtherProj";
import DesignProjects from "../components/Projects/DesignProj";

class Projects extends Component {
    // fetch the projects data & load into the redux store
    componentDidMount() {
        document.title = "Projects | Dhyey Thumar";

        this.props.fetchFeatProjData();
        this.props.fetchOtherProjData();
        this.props.fetchDesignProjData();
    }

    render() {
        return (
            <MainContainer>
                {/* featured projects section */}
                <FeaturedProjects />

                {/* other noteworthy projects section */}
                <OtherProjects />

                {/* design projects section */}
                <DesignProjects />
            </MainContainer>
        );
    }
}

// dispatch actions
const mapDispatchToProps = (dispatch) => {
    return {
        fetchFeatProjData: () => dispatch(initFeatProj()),
        fetchOtherProjData: () => dispatch(initOtherProj()),
        fetchDesignProjData: () => dispatch(initDesignProj()),
    };
};

export default connect(null, mapDispatchToProps)(Projects);
