import { useState } from "react";
import { Spinner } from "../UI/spinner";

export const ProjImage = (props) => {
    const [loaded, setLoaded] = useState(false);

    return (
        <span className="mb-3 xs:mb-4 lg:mb-5">
            <img
                onLoad={() => setLoaded(true)}
                className={`${loaded ? `visible` : `invisible`} ${
                    props.customClasses
                } h-auto rounded-sm xxs:rounded-md lg:rounded-lg`}
                src={props.imgUrl}
                alt={props.imgAltText}
                loading="lazy"
            ></img>
            <div
                className={`${
                    loaded ? `hidden` : ``
                } bg-secondary bg-opacity-20 h-32 xxxs:h-44 xxs:h-52 xs:h-60 sm:h-64 md:h-48 lg:h-60 xl:h-80 z-0 min-w-full rounded-sm xxs:rounded-md lg:rounded-lg`}
            >
                <Spinner />
            </div>
        </span>
    );
};

export const ProjTitle = (props) => (
    <span
        className={
            "heading font-source-serif-semibold lg:text-xl mb-1 sm:mb-2 md:mb-1 lg:mb-2 " +
            props.customClasses
        }
    >
        {props.children}
    </span>
);

export const ProjDate = (props) => (
    <p
        className={
            "submain mb-3 sm:mb-4 md:mb-3 lg:mb-5 " + props.customClasses
        }
    >
        {props.children}
    </p>
);

// !! can have standary content class here !!
export const ProjDesc = (props) => (
    <p
        className={
            "font-manrop leading-5 xxs:leading-6 md:leading-5 lg:leading-7 tracking-wide text-xs xxs:text-sm md:text-xs lg:text-base text-justify " +
            props.customClasses
        }
    >
        {props.children}
    </p>
);

export const ExpandProjList = (props) => {
    return (
        <div className="flex items-center justify-center mt-6 xs:mt-8 md:mt-10 lg:mt-12">
            <button
                onClick={() => {
                    if (props.totalOpenCards < props.projLen)
                        props.setTotalOpenCards(props.totalOpenCards + 2);
                    else props.setTotalOpenCards(props.initialCardToDisplay);
                }}
                className="text-main px-5 lg:px-6 py-3 box-border-md"
            >
                <span className="font-manrop-bold text-xs lg:text-sm">
                    {props.totalOpenCards < props.projLen ? (
                        <p>
                            Show&nbsp;&nbsp;
                            {props.projLen - props.totalOpenCards}
                            &nbsp;&nbsp;More
                        </p>
                    ) : (
                        <p>Show Less</p>
                    )}
                </span>
            </button>
        </div>
    );
};
