import NavigationItems from "./NavigationItems";

const DropDown = (props) => {
    let dropDown = null;
    if (props.open)
        dropDown = (
            <div className="max-w-xs mx-auto md:hidden">
                <NavigationItems dropDownHandler={props.dropDownHandler} />
            </div>
        );
    return dropDown;
};

export default DropDown;
