import {
    CloudMoon,
    CloudSun,
    Sun,
    GitHub,
    LinkedIn,
    Medium,
    YouTube,
    Bulb,
} from "./UI/icons";

const Button = (props) => (
    <button
        onClick={() => props.toggleTheme(props.selfThemeType)}
        className={
            props.currentThemeType === props.selfThemeType
                ? "opacity-100"
                : "opacity-50 hover:opacity-80"
        }
    >
        {props.children}
    </button>
);

const AnchorTag = (props) => (
    <a href={props.link} className="opacity-80 hover:opacity-100">
        {props.children}
    </a>
);

const ExternalLinks = () => {
    const classes = "w-7 sm:w-8";
    return (
        <div className="mx-auto w-full xxxs:w-10/12 xxs:w-8/12 xs:w-7/12 sm:w-6/12 md:w-5/12 lg:w-4/12 pt-4 flex justify-around border-footer border-t-2 border-solid">
            <AnchorTag link="https://github.com/dhyeythumar">
                <GitHub customClasses={classes} />
            </AnchorTag>
            <AnchorTag link="https://www.linkedin.com/in/dhyeythumar/">
                <LinkedIn customClasses={classes} />
            </AnchorTag>
            <AnchorTag link="https://dhyeythumar.medium.com/">
                <Medium customClasses={classes} />
            </AnchorTag>
            <AnchorTag link="https://www.youtube.com/channel/UCpKizIKSk8ga_LCI3e3GUig">
                <YouTube customClasses={classes} />
            </AnchorTag>
        </div>
    );
};

const Footer = (props) => {
    const dateTime = new Date()
        .toLocaleString("en-in", {
            timeZone: "Asia/Kolkata",
            hour: "numeric",
            minute: "numeric",
            hour12: true,
        })
        .toUpperCase();

    return (
        <footer className="bg-footer-color text-footer-color" id="footer">
            <div className="section-size py-6 px-6 sm:px-10 space-y-6">
                <span className="flex flex-row justify-center lg:justify-end space-x-8">
                    <Button selfThemeType="night" {...props}>
                        <CloudMoon customClasses="w-5 lg:w-6" />
                    </Button>
                    <Button selfThemeType="morning" {...props}>
                        <CloudSun customClasses="w-6 lg:w-7" />
                    </Button>
                    <Button selfThemeType="evening" {...props}>
                        <Sun customClasses="w-5 lg:w-6" />
                    </Button>
                </span>

                <div className="w-auto">
                    <div className="w-4/12 mx-auto mb-2 font-gotu text-2xl sm:text-3xl lg:text-4xl text-center">
                        dh.
                    </div>
                    <ExternalLinks />
                </div>

                <div className="text-xs sm:text-sm tracking-wider font-source-serif text-center space-y-2">
                    <div className="lg:py-2 lg:pl-5 border-footer lg:border-l-2 lg:text-left space-y-2">
                        <div className="inline-flex sm:space-x-2 align-middle">
                            <Bulb customClasses="hidden sm:block w-4 lg:w-5" />
                            <span>
                                It's currently {dateTime} in Dhyey's city
                            </span>
                        </div>
                        <div>Last Update: July 2021</div>
                    </div>

                    <div className="w-full">
                         {new Date().getFullYear()} Dhyey Thumar
                    </div>
                </div>
            </div>
        </footer>
    );
};

export default Footer;
